    -webkit-appearance: none;
    margin: 0
  }

  .el-pagination__editor:focus {
    outline: 0;
    border-color: #20a0ff
  }

  .el-pager {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    list-style: none;
    font-size: 0;
    padding: 0
  }

  .el-pager li {
    padding: 0 4px;
    border: 1px solid #d1dbe5;
    border-right: 0;
    background: #fff;
    font-size: 13px;
    min-width: 50px;
    height: 50px;
    line-height: 50px;
    box-sizing: border-box;
    text-align: center
  }

  .el-pager li:last-child {
    border-right: 1px solid #d1dbe5
  }

  .el-pager li.btn-quicknext, .el-pager li.btn-quickprev {
    line-height: 50px;
    color: #97a8be
  }

  .el-pager li.active + li {
    border-left: 0;
    padding-left: 5px
  }

  .el-pager li:hover {
    color: #20a0ff
  }

  .el-pager li.active {
    border-color: #20a0ff;
    background-color: #20a0ff;
    color: #fff;
    cursor: default
  }

  @font-face {
    font-family: element-icons;
    src: url(../../../static/fonts/element-icons.woff?t=1472440741) format('woff'), url(../../../static/fonts/element-icons.ttf?t=1472440741) format('truetype');
    font-weight: 400;
    font-style: normal
  }

  [class*=" el-icon-"], [class^=el-icon-] {
    font-family: element-icons !important;
    speak: none;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    vertical-align: baseline;
    display: inline-block;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
  }

  .el-icon-arrow-down:before {
    content: "\e600"
  }

  .el-icon-arrow-left:before {
    content: "\e601"
  }

  .el-icon-arrow-right:before {
    content: "\e602"
  }

  .el-icon-arrow-up:before {
    content: "\e603"
  }

  .el-icon-caret-bottom:before {
    content: "\e604"
  }

  .el-icon-caret-left:before {
    content: "\e605"
  }

  .el-icon-caret-right:before {
    content: "\e606"
  }

  .el-icon-caret-top:before {
    content: "\e607"
  }

  .el-icon-d-arrow-left:before {
    content: "\e60e"
  }

  .el-icon-d-arrow-right:before {
    content: "\e60f"
  }

  .el-icon-d-caret:before {
    content: "\e610"
  }

  .el-icon-more:before {
    content: "\e61a"
  }

  @keyframes rotating {
    0% {
      transform: rotateZ(0)
    }
    100% {
      transform: rotateZ(360deg)
    }
  }

</style>



